.stars {
  display: flex;

  &__star {
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
    margin-right: 4px;

    &:last-child {
      margin-right: 0;
    }
  }

  @for $index from 1 through 5 {
    &--#{$index} :nth-child(-n+#{$index}) {
      background-image: url("../images/star-active.svg");
    }
  }
}
